import React from "react";
import "react-image-gallery/styles/css/image-gallery.css";

const DrainageRoof = () => (
  <div className="Drainage_roof mrg-top">
    <div className="Drainage-roof_special-container roof_special-container">
      <h1 className="Drainage_roof-title roof-title">Водостічні системи</h1>
      <p className="Drainage-roof_special-text roof_special-text">
        Ефективна водостічна система захищає дах та весь будинок від руйнівних
        наслідків вологи. Ми встановлюємо, ремонтуємо та модернізуємо водостічні
        жолоби, труби та аксесуари. Наші фахівці допомагають вибрати найбільш
        підходящий варіант, що забезпечує довговічність та захист від засорів.
      </p>
      <div className="Drainage_roof_bot-div roof_bot-div">
        <h3> Переваги:</h3>
        <ul className="special-text_list">
          <li>Можливість використання систем під будь-який тип даху.</li>
          <li>Міцні матеріали, стійкі до перепадів температур.</li>
          <li>Естетичний вигляд, що доповнює дизайн будівлі.</li>
        </ul>
      </div>
      <div className="Drainage-roof_btn-container btn-special_container">
        <a
          href="/*"
          className="feedback-link"
          // onClick={(event) => {
          //   event.preventDefault();
          //   setIsModalOpen(true);
          // }}
        >
          <div className="feedback-container">
            <div className="feedback-border">
              <div className="feedback">
                <div className="feedback-text-container">
                  <p className="feedback-text">Замовити дзвінок</p>
                </div>
              </div>
            </div>
          </div>
        </a>
      </div>
    </div>

    <img
      className="Drainage-roof_special-img roof_special-img"
      src="./img/Водосточка.jpg"
      alt=""
    ></img>
  </div>
);

export default DrainageRoof;
